@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&family=Varela+Round&family=Zen+Kaku+Gothic+Antique:wght@300;500&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}*{box-sizing:border-box;border:0}body{font-family:"Comfortaa",Arial,cursive;color:#ebebeb;min-height:100vh}

article{text-align:center}article .poster{border:1px solid #131313;height:300px;border-radius:7px;margin-bottom:.7em;transition:-webkit-transform 100ms ease-in-out;transition:transform 100ms ease-in-out;transition:transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out}article .poster:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}article .poster:hover{cursor:pointer}article svg{width:1.3em}

.clicked-movie{width:100%;display:flex;justify-content:center}.clicked-image{align-items:flex-end;background-size:cover;background-position:center;border-radius:10px;display:flex;height:100vh;justify-content:center;padding:1.5em;text-align:left;width:70%}.clicked-image .movie-stats{background-color:rgba(0,0,0,0.75);border-radius:10px;box-shadow:0 10px 9px -9px rgba(0,0,0,0.8);min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;padding:1em;width:95%}.clicked-image .movie-stats h3{font-size:2.2em;margin-bottom:.4em}.clicked-image .movie-stats p{padding:.2em 0}.x-button{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin-right:1.2em}.x-button button{background-color:transparent;color:#ebebeb;font-family:"Comfortaa",Arial,cursive;font-size:3.5em;transition:-webkit-transform 100ms ease-in-out;transition:transform 100ms ease-in-out;transition:transform 100ms ease-in-out, -webkit-transform 100ms ease-in-out}.x-button button:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.x-button button:hover{cursor:pointer}

main{background-color:black;min-height:100vh;padding:2em 3.5em;text-align:right}h2{text-align:center}.movie-grid{display:grid;grid-template-columns:repeat(auto-fit, minmax(210px, 1fr));grid-gap:2em}.loading{display:flex;flex-direction:column;align-items:center;justify-content:space-between;height:7em}

.error-container{background-image:url(https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=870&q=80);background-size:cover;background-position-y:-250px;background-position-x:center;height:80vh;width:100%;text-align:center}h2{font-size:1.5em;margin-bottom:.7em}.spooky-error{font-size:1.2em;margin-bottom:1.5em}.error-link{color:#b73203;padding:.5em;border-radius:7px;font-size:1.5em}.error-link:hover{border:1px solid #b73203}

select{background:transparent;border-radius:7px;box-shadow:0 5px 8px -3px rgba(235,235,235,0.5);color:#ebebeb;font-family:"Comfortaa",Arial,cursive;font-size:.9em;height:3em;margin-bottom:2em;padding:2px;width:25%}select:hover{cursor:pointer}

header{background:linear-gradient(to bottom, transparent, black),url(/sour-pomodoros/static/media/tomatoes.ea601dbd.jpeg);background-size:cover;height:5em;font-size:2.7em;display:flex;align-items:flex-end}header .header-link{text-decoration:none;color:#ebebeb}header h1{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;margin:0 0 .2em .7em;font-size:1.2em}

