main {
  background-color: black;
  min-height: 100vh;
  padding: 2em 3.5em;
  text-align: right;
}

h2 {
  text-align: center;
}

.movie-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(210px, 1fr));
  grid-gap: 2em;
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 7em;
}